import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

import { navigate } from 'gatsby-link'
import HomeTitle from '../components/homepage/homeTitle'
import {Container, VerticalPadding} from '../styles/globalStyles'

import '../styles/css/styles.scss'

function encode(data) {
  return Object.keys(data)
    .map((key) => encodeURIComponent(key) + '=' + encodeURIComponent(data[key]))
    .join('&')
}

const Portfolio = () => {

  const [state, setState] = React.useState({})

  const handleChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    const form = e.target
    fetch('/', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: encode({
        'form-name': form.getAttribute('name'),
        ...state,
      }),
    })
      .then(() => navigate(form.getAttribute('action')))
      .catch((error) => alert(error))
  }

  return(
    <Layout>
      <SEO title="Contacte-nos" description="Entre em contacto connosco. Teremos todo o gosto em ajudar!" />
      <HomeTitle title="Fale connosco." leading="@pathfinderdesigns" />
      
      {/* <VideoCaption video={captionVideo} caption="Use o formulário abaixo para qualquer dúvida. Teremos todo o gosto em ajudar!" /> */}
      {/* <Contact /> */}

      <Container>
        <p>Entre em contacto e responderemos o mais rapidamente possível.</p>
        <VerticalPadding>

        
          
          <div style={{marginBottom: '-30px  '}}></div>
          <div className="banner">
            
          
          
          <form
            name="Contactos"
            method="post"
            action="/thank-you/"
            data-netlify="true"
            data-netlify-honeypot="bot-field"
            onSubmit={handleSubmit}
          >
            {/* The `form-name` hidden field is required to support form submissions without JavaScript */}
            <input type="hidden" name="form-name" value="Contactos" />
            <p hidden>
              <label htmlFor="bot-field">
                Don’t fill this out: <input name="bot-field" onChange={handleChange} />
              </label>
            </p>
              <label htmlFor="name">
                Name <span className="req"> *</span>
                <br />
                <input id="name" type="text" name="name" onChange={handleChange} required/>
              </label>
              <label htmlFor="email">
                Email <span className="req"> *</span>
                <br />
                <input id="email" type="email" name="email" onChange={handleChange} required/>
              </label>
              <label htmlFor="msg">
                Message <span className="req"> *</span>
                <br />
                <textarea id="msg" name="message" onChange={handleChange} required/>
              </label>            
              <button className="send-button" type="submit">Submit »</button>
          </form>

          </div>
        </VerticalPadding>
      </Container>
     
    </Layout>
  )
  
}

export default Portfolio
